




















d3 = require 'd3'
_  = require 'lodash'

module.exports =
    name: 'slider-base'
    props: ['label', 'value', 'max', 'min', 'step', 'bounded']
    methods:
        update: (value) ->

            _number = Number(value)

            if _.isNaN(_number)
                number = 0
            else
                number = _number

            if @bounded
                if number > @max
                    number = _.min([@max, number])
                else if number < @min
                    number = _.max([@min, number])

            @$emit('input', number)

        increment: () ->
            _newValue = Number(@value) + Number(@step)
            newValue = Math.round(100*_newValue) / 100

            @update(newValue)

        decrement: () ->
            _newValue = Number(@value) - Number(@step)
            newValue = Math.round(100*_newValue) / 100

            @update(newValue)

        wheel: (direction) ->

            if direction > 0
                @increment()
            if direction < 0
                @decrement()




